import React from "react"



export default function headfoot(props) {
    const topbar = {
        backgroundColor: "white",
        color: "red",
        height: "8vh",
        textAlign: "center",
        fontSize: "3vh", 
        fontWeight: "bold",
        padding: "2.5vh",
        boxSizing: "border-box",
    }

    return (
        <div class="wholepage">
            <div style={topbar}>
                {props.location}
            </div>
            {props.children}
            {/* <div style={{height:"20vh"}}></div> */}
        </div>
    )
}
